import React from 'react'
import './index.scss'
import { GatsbyImage, getImage } from "gatsby-plugin-image"

export const CoverImage = ({ image, imageFluid, title }) =>
  <div>
    {image ?
      <img className='cover-image'
           src={image}
           alt='title' />
      :
      <GatsbyImage className='cover-image'
             alt={title}
             image={getImage(imageFluid)} />
    }
    <div className='cover-image-overlay' />
  </div>
